import React from "react";
import Modal from "react-modal";
import { Box, ModalInput, ModalTextArea, Form } from "../styles";
import { BoxBtn, WhiteBoxBtn } from "../components/Btn";
import "../assets/css/modal-style.css";

Modal.setAppElement("#root");

export default function ModalWindow(props) {
  const [modalIsOpen, setIsOpen] = React.useState(false);
  return (
    <div className="App">
      <Box onClick={() => setIsOpen(true)}>{props.children}</Box>
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={() => setIsOpen(false)}
        overlayClassName={{
          base: "overlay-base",
          afterOpen: "overlay-after",
          beforeClose: "overlay-before",
        }}
        className={{
          base: "content-base",
          afterOpen: "content-after",
          beforeClose: "content-before",
        }}
        closeTimeoutMS={500}
      >
        <Form p={10} action="https://formspree.io/f/mgepawdl" method="POST">
          <Box px={14}>
            <ModalTextArea
              mt={20}
              cols={60}
              name="message"
              placeholder="ここにメッセージを入力してください。"
              color="softBlack"
            ></ModalTextArea>
          </Box>
          <ModalInput placeholder="メールアドレス" type="email" name="email" />

          <Box my={20} borderBottom="solid 1px #E3E5ED" width="100%"></Box>
          <Box m={14} className="flex flex-end">
            <WhiteBoxBtn
              height={40}
              fontSize={14}
              ml="auto"
              onClick={() => setIsOpen(false)}
            >
              キャンセル
            </WhiteBoxBtn>
            <BoxBtn height={40} fontSize={14} type="submit">
              送信
            </BoxBtn>
          </Box>
        </Form>
      </Modal>
    </div>
  );
}
