const settings = {
  dots: false,
  infinite: true,
  speed: 500,
  slidesToShow: 2,
  slidesToScroll: 1,
  centerPadding: "16%",
  centerMode: true,
  nextArrow: <SampleNextArrow />,
  prevArrow: <SamplePrevArrow />,
  waitForAnimate: false,
  responsive: [
    {
      breakpoint: 1080,
      settings: {
        slidesToShow: 2,
      },
    },
    {
      breakpoint: 640,
      settings: {
        slidesToShow: 1,
      },
    },
  ],
};

export default class SimpleSlider extends Component {
  constructor(props) {
    super(props);
    this.state = {
      category: "wedding",
    };
  }

  render() {
    return (
      <Box>
        <Box className="flex border-box" px={80}>
          {[
            ["wedding", "フォトウエディング"],
            ["outdoor", "イベント・アウトドア"],
            ["person", "人物撮影"],
            ["else", "その他"],
          ].map((item, index) => {
            return (
              <Text
                key={index}
                className="pointer border-box"
                mr={20}
                mb={20}
                pb={4}
                fontFamily="TsukuARdGothicBold"
                fontSize={16}
                color={item[0] === this.state.category ? "black" : "grey7"}
                borderBottom={
                  item[0] === this.state.category ? "solid 1px #000" : "none"
                }
                onClick={() => this.setState({ category: item[0] })}
              >
                {item[1]}
              </Text>
            );
          })}
        </Box>
        <MotionDiv
          animate={{
            opacity: 1,
          }}
          initial={{
            opacity: 0,
          }}
          exit={{
            opacity: 0,
          }}
          transition={{ duration: 0.5 }}
        >
          <Slider ref={(c) => (this.slider = c)} {...settings}>
            {urls[this.state.category].map((url, index) => {
              return (
                <Box key={index}>
                  <ImageBox className="relative" url={url}>
                    <Box
                      py={14}
                      px={20}
                      backgroundColor="white"
                      className="inline absolute"
                      bottom={6}
                      left={0}
                    >
                      <Text
                        fontSize={[12, 14]}
                        color="grey8"
                        fontFamily="TsukuARdGothic"
                        className="inline"
                      >
                        {this.state.category + "-" + String(index + 1)}
                      </Text>
                    </Box>
                  </ImageBox>
                </Box>
              );
            })}
          </Slider>
        </MotionDiv>
      </Box>
    );
  }
}

const ImageBox = styled.div`
  width: 95%;
  &:before {
    content: "";
    display: block;
    padding-top: 125%;
  }
  margin-left: 10px;
  margin-right: 10px;
  background-position: center;
  background-size: cover;
  background-image: ${(props) => "url(" + props.url + ")"};
`;
